import React from 'react';
import { makeStyles, TableBody, TableCell, TableHead, Table } from '@material-ui/core';
import TableContainer from '@material-ui/core/TableContainer';
import TableRow from '@material-ui/core/TableRow';

const CustomTable: React.FC<Props> = ({ rows, columns }) => {
  const classes = useStyles();
  return (
    <div className={classes.table}>
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell key={column.key} component='th'>{column.name}</TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.id}>
                {columns.map((column) => (
                  <TableCell key={column.key} component='td'>{row[column.key]}</TableCell>
                ))}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  )
}

const useStyles = makeStyles((theme) => ({
  table: {},
}))

interface Props {
  rows: any[];
  columns: any[];
}

export default CustomTable;