import React from 'react';
import { makeStyles } from '@material-ui/core';

import Table from '../../components/Table';
import { rows, columns } from './constants'
import ApiClient from '../../services/apiClient';

const useStyles = makeStyles((theme) => ({
  root: {
  },
}));

const metrics = ['spend', 'impressions', 'cpc', 'ctr', 'clicks','conversion', 'cost_per_conversion', 'conversion_rate']

const Dashboard = () => {
  const classes = useStyles();
  const [statistics, setStatistics] = React.useState<any[]>([]);

  React.useEffect(() => {
    const loadAdvertisers = async () => {
      const { list: advertisers }: any = await ApiClient.GET('/advertiser/get', { isOauth: true });
      const ids: string[] = advertisers.map(({ advertiser_id: id }: any) => id);
      const today = new Date();
      const date = `${today.getFullYear()}-${today.getMonth()}-${today.getDay()}`
      const stats: any[] = await Promise.all(
        ids.map(async (id: string) => {
          const { list: reports }: any = await ApiClient.GET('/reports/integrated/get', {
            params: {
              advertiser_id: id,
              report_type: 'BASIC',
              start_date: date,
              end_date: date,
              metrics,
              dimensions: ['stat_time_day'],
              data_level: 'AUCTION_ADVERTISER',
            },
          });
          const advertiser = advertisers.find((item: any) => item.advertiser_id === id)
          return {
            ...advertiser,
            ...reports[0].metrics,
          };
        })
      );
      console.log(stats)
      setStatistics(stats);
    }
    loadAdvertisers();
  })
  return (
    <div className={classes.root}>
      <Table rows={statistics} columns={columns} />
    </div>
  )
}

export default Dashboard;